<template>
<im-container>
  <im-header class="header" @click.native="controlSideBar"></im-header>
  <im-container>
    <im-side-bar class="Left">
      <router-link to="/">首页</router-link>
      <router-link to="/intern">实习申请</router-link>
      <router-link to="/info">实习信息</router-link>
    </im-side-bar>
    <im-main class="Middle">
      <router-view></router-view>
    </im-main>
  </im-container>
</im-container>
</template>

<script>
import imContainer from './components/imContainer'
import imHeader from './components/imHeader'
import imMain from './components/imMain'
// import imModuleBox from './components/imModuleBox'
import imSideBar from './components/imSideBar'
export default {
  name: 'App',
  components: {
    imContainer,
    imHeader,
    imMain,
    // imModuleBox,
    imSideBar
  },
  methods: {
    controlSideBar () {
      // Code that will run only after the
      // entire view has been rendered
      console.log('click listening')

      var theBody = document.querySelector('body')
      // This function merely toggles the class
      function toggleClass () {
        theBody.classList.toggle('OffCanvas-Active')
      }
      toggleClass()
      // When the header is clicked we fire the function to toggle the class
      // document.querySelector('.Header').addEventListener('click', toggleClass)

      // The section below merely deals with resize events

      // This debounce function (via: https://remysharp.com/2010/07/21/throttling-function-calls) merely stops functioned firing too often on repetitive events (such as resize/scroll)
      // function debounce (fn, delay) {
      //   var timer = null
      //   return function () {
      //     var context = this
      //     var args = arguments
      //     clearTimeout(timer)
      //     timer = setTimeout(function () {
      //       fn.apply(context, args)
      //     }, delay)
      //   }
      // }

      // removing the class from the body inside a debounce
      // var debouncedA = debounce(function () {
      //   theBody.classList.remove('OffCanvas-Active')
      // }, 250)

      // // When the window is resized, we want to fire the debouncedA function
      // window.onresize = debouncedA
    }
  }



}
</script>

<style lang="stylus">
@import "./assets/css/normalize";

.OffCanvas-Active .Left,
.OffCanvas-Active .Middle {
  transform: translate3d(200px, 0, 0);
  transition: transform .3s;
}

// .header:hover + * .left,
// .header:hover + * .Middle{
//   transform: translate3d(200px, 0, 0);
//   transition: transform .3s;
// }
</style>




